.icon {
  font-size: 16px;
}

.icon:before {
  content: ' ';
  vertical-align: middle;
  display: inline-block;
  background-image: url("../images/svg/ai-svg-sprite.svg");
  background-repeat: no-repeat;
  background-size: 4.25em 245.25em;
}

.no-svg .icon:before {
  background-image: url("../svg/ai-svg-sprite.png");
}


